___CSS_LOADER_EXPORT___.push([module.id, `* {
    box-sizing: border-box;
    margin: 0;
}

#container {
    display: grid;
    grid-template-rows: 100px 500px;
    grid-template-columns: 1fr 1fr;
    height: 100vh;
    background-color: #D6F4ED;
}

#header {
    grid-row: 1 / 2;
    grid-column: 1 / 3;
    border-bottom: 5px solid black;
    display: grid;
    grid-template-rows: 1 fr;
    grid-template-columns: 1fr 2fr 0.5fr 0.5fr;
}

#title {
    grid-row: 1 / 2;
    grid-column: 1 / 2;
    font-size: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
}

#instruction {
    grid-row: 1 / 2;
    grid-column: 2 / 3;
    font-size: 28px;
    display: flex;
    justify-content: center;
    align-items: center;
}

#restartBtn {
    grid-row: 1 / 2;
    grid-column: 3 / 4;
    justify-self: center;
    align-self: center;
    text-align: center;
    font-size: 16px;
    height: 30px;
    width: 75px;
    background-color: #f5f5f5;
    border-radius: 20px;
}

#changeMode {
    grid-row: 1 / 2;
    grid-column: 4 / 5;
    justify-self: center;
    align-self: center;
    text-align: center;
    font-size: 16px;
    height: 50px;
    width: 90px;
    background-color: #f5f5f5;
    border-radius: 20px;
}

#passBtn {
    font-size: 16px;
    height: 30px;
    width: 75px;
    background-color: #f5f5f5;
    border-radius: 20px;
}

.content {
    display: flex;
    flex-direction: row;
}

.playerWindow {
    /* border: 5px solid red; */
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    font-size: 20px;
}

#window1 {
    grid-row: 2 / 3;
    grid-column: 1 / 2;
}

#window2 {
    grid-row: 2 / 3;
    grid-column: 2 / 3;
}

.info  {
    display: flex;
    flex-direction: column;
    text-align: center;
    margin: 25px;
}

.shipIcon {
    cursor: grab;
}

.mapContainer {
    display: grid;
    grid-template-rows: 30px 1fr;
    grid-template-columns: 30px 1fr;
}

.upperCoords {
    grid-row: 1 / 2;
    grid-column: 2 / 3;
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: repeat(10, 1fr);
}

.upperCoords div {
    display: flex;
    align-items: center;
    justify-content: center;
}

.leftCoords {
    grid-row: 2 / 3;
    grid-column: 1 / 2;
    display: grid;
    grid-template-rows: repeat(10, 1fr);
    grid-template-columns: 1fr;
}

.leftCoords div {
    display: flex;
    align-items: start;
    justify-content: center;
}

.map {
    grid-row: 2 / 3;
    grid-column: 2 / 3;
    border: 5px solid #473472;
    background-color: #87BAC3;
    height: 300px;
    width: 300px;
    display: grid;
    grid-template-rows: repeat(10, 1fr);
    grid-template-columns: repeat(10, 1fr);
    text-align: center;
    border-radius: 15px;
}

.map div {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px dotted black;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,+BAA+B;IAC/B,8BAA8B;IAC9B,aAAa;IACb,yBAAyB;AAC7B;;AAEA;IACI,eAAe;IACf,kBAAkB;IAClB,8BAA8B;IAC9B,aAAa;IACb,wBAAwB;IACxB,0CAA0C;AAC9C;;AAEA;IACI,eAAe;IACf,kBAAkB;IAClB,eAAe;IACf,aAAa;IACb,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,eAAe;IACf,kBAAkB;IAClB,eAAe;IACf,aAAa;IACb,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,eAAe;IACf,kBAAkB;IAClB,oBAAoB;IACpB,kBAAkB;IAClB,kBAAkB;IAClB,eAAe;IACf,YAAY;IACZ,WAAW;IACX,yBAAyB;IACzB,mBAAmB;AACvB;;AAEA;IACI,eAAe;IACf,kBAAkB;IAClB,oBAAoB;IACpB,kBAAkB;IAClB,kBAAkB;IAClB,eAAe;IACf,YAAY;IACZ,WAAW;IACX,yBAAyB;IACzB,mBAAmB;AACvB;;AAEA;IACI,eAAe;IACf,YAAY;IACZ,WAAW;IACX,yBAAyB;IACzB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,2BAA2B;IAC3B,aAAa;IACb,sBAAsB;IACtB,sBAAsB;IACtB,mBAAmB;IACnB,eAAe;AACnB;;AAEA;IACI,eAAe;IACf,kBAAkB;AACtB;;AAEA;IACI,eAAe;IACf,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,kBAAkB;IAClB,YAAY;AAChB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,4BAA4B;IAC5B,+BAA+B;AACnC;;AAEA;IACI,eAAe;IACf,kBAAkB;IAClB,aAAa;IACb,uBAAuB;IACvB,sCAAsC;AAC1C;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;IACI,eAAe;IACf,kBAAkB;IAClB,aAAa;IACb,mCAAmC;IACnC,0BAA0B;AAC9B;;AAEA;IACI,aAAa;IACb,kBAAkB;IAClB,uBAAuB;AAC3B;;AAEA;IACI,eAAe;IACf,kBAAkB;IAClB,yBAAyB;IACzB,yBAAyB;IACzB,aAAa;IACb,YAAY;IACZ,aAAa;IACb,mCAAmC;IACnC,sCAAsC;IACtC,kBAAkB;IAClB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,wBAAwB;AAC5B","sourcesContent":["* {\n    box-sizing: border-box;\n    margin: 0;\n}\n\n#container {\n    display: grid;\n    grid-template-rows: 100px 500px;\n    grid-template-columns: 1fr 1fr;\n    height: 100vh;\n    background-color: #D6F4ED;\n}\n\n#header {\n    grid-row: 1 / 2;\n    grid-column: 1 / 3;\n    border-bottom: 5px solid black;\n    display: grid;\n    grid-template-rows: 1 fr;\n    grid-template-columns: 1fr 2fr 0.5fr 0.5fr;\n}\n\n#title {\n    grid-row: 1 / 2;\n    grid-column: 1 / 2;